.env_post_grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 30px;
  padding: 30px 50px 30px 50px;
}
.env_post_item_container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  border: 1px solid #9E9E9E;
  margin-bottom: 10px;
}
.env_post_item_container img {
  max-width: 100%;
  height: 150px;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.pagination button {
  width: 30px;
  height: 30px;
  border: 1px solid #8DC693;
  background-color: transparent;
  color: #333;
  font-size: 14px;
  margin: 0 5px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.pagination button:hover {
  background-color: #8DC693;
  color: #fff;
}

.pagination button.active {
  background-color: #8DC693;
  color: #fff;
}