* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.mypage_main_container {
  display: flex;
  flex-direction: column;
  justify-content: LEFT;
  width: 92%;
  padding: 120px 0 0 280px;
  margin-bottom: 50px;
}

.my_posts {
  margin: 35px 0 15px;
}

ul {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

li {
  list-style: none;
}

.custom_button {
  background-color: white;
  border: 1px solid #8DC693;
  color: #2B2B2B;
  cursor: pointer;
}

.custom_button.active {
  background-color: #8DC693;
  color: white;
}

.mypage_posts_container {
  height: auto;
  display: flex;
  flex: 0 1 0;
  flex-direction: column;
  gap: 10px;
  margin-top: 15px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.post {
  display: flex;
  flex-direction: column;
  gap: 5px;
  flex: 0 1 auto;
  padding: 25px 15px 25px;
  margin: 0 20px;
  border-bottom: 1px solid #D9D9D9;
}

.post_info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.post_title {
  font-size: 15pt;
}

.mypage_post_content {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.posts_container > article:first-child {
  margin-top: 15px;
}

.posts_container > article:last-child {
  margin-bottom: 5px;
  border-bottom: 0;
}